* {
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: 0.15s ease;
}

body {
  background: #d5d5d5;
  font-family: "Hind", sans-serif;
}

.danger {
  background: rgb(209, 36, 36);
}
.primary {
  background: rgba(0, 138, 255, 0.93);
}

.input-wrap {
  display: flex;
  justify-content: center;
  padding: 15px 0;
  background: #fff;
}
.input-wrap input {
  color: rgb(49, 49, 49);
}

footer {
  background: rgb(49, 49, 49);
  padding: 10px 15px;
  text-align: center;
}
.footer-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 600px;
  margin: 0 auto;
}
footer p {
  color: #fff;
}
footer p a {
  color: rgb(209, 36, 36);
  text-decoration: none;
  text-shadow: 0px 0px 2px #8c8c8c;
}
footer #github-logo {
  display: block;
  width: 30px;
  height: 30px;
  background: url(./images/github-logo.svg) no-repeat center / contain;
}

.content-wrap {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  box-shadow: 0px 1px 8px rgb(160, 150, 150);
  padding: 10px 15px;
  border-radius: 3px;
}
.input-wrap input {
  padding: 10px 15px;
  display: block;
  box-shadow: 0px 1px 5px rgb(160, 150, 150);
  border-radius: 3px;
}
.input-wrap input:focus {
  box-shadow: 0px 1px 8px rgb(94, 92, 92);
}
.input-wrap button {
  margin-left: 5px;
}

button:hover {
  box-shadow: 0px 1px 6px rgb(94, 92, 92);
}
button:active {
  opacity: 0.7;
}

/* Todos */
.todos-wrap {
  padding: 15px 20px;
}
.todos-wrap h2 {
  text-align: center;
  color: rgb(49, 49, 49);
}
.todo-wrap {
  background: #fff;
  box-shadow: 0px 1px 5px rgb(160, 150, 150);
  padding: 15px 20px;
  max-width: 600px;
  margin: 10px auto;
}

.todo-wrap .danger {
  margin-left: 5px;
}
.todo-title {
  font-size: 25px;
  color: rgb(49, 49, 49);
}
.todo-date {
  font-size: 13px;
  color: #b7b7b7;
  margin: 2px 0 15px 0;
}

.line-through {
  text-decoration: line-through;
}

/* Loader */
.loader-wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(85, 85, 85, 0.452);
}
.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-ellipsis div {
  position: absolute;
  top: 33px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: rgba(0, 138, 255, 0.93);
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
  left: 8px;
  animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
  left: 8px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
  left: 32px;
  animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
  left: 56px;
  animation: lds-ellipsis3 0.6s infinite;
}
@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
